import React from "react";
import { useParams, useNavigate } from 'react-router-dom';
import o1 from '../images/1.png';
import o2 from '../images/2.png';
import o3 from '../images/3.png';
import o4 from '../images/4.png';
import o5 from '../images/5.png';
import '../styles/item-catalog.css';


/**
 * !   
 */
export interface ItemProduct {
    id: number
    name: string
    description: string
    images: string[]
    price?: {
        old: number
        current: number
    }
    property?: {
        width: number
        height: number
        count: number
    }
}
// *   
export const items: ItemProduct[] = [
    { 
        id: 0, 
        images: [o1, o2, o3, o4, o5], 
        name: '      0.8',
        description: '       ()          , ,   .',
        price: {
            old: 334,
            current: 301
        }
    },
    {
        id: 1,
        images: [o1, o2, o3, o4, o5],
        name: '      1.0',
        description: '     . ',
        price: {
            old: 40,
            current: 350
        }
    }
];



/**
 *  ()    
 *     
 * 
 */
export default function Catalog({  }) {
    const navigate = useNavigate();
    
    

    return (
        <React.Fragment>
            {items.map((item, index)=> 
                <div style={{cursor: 'pointer'}}
                    key={index}
                    onClick={()=> navigate(`/catalog/item/${item.id}`)}
                >
                    { item.name }
                </div>
            )}
        </React.Fragment>
    );
}