import React from "react";
import { Box, Paper, Typography } from "@mui/material";
import { ItemProduct } from '../index';


type DescriptionBlockPriceProps = {
    item: ItemProduct
}


//todo:         9999
export default function BlockPrice ({ item }: DescriptionBlockPriceProps) {
    const calculate =()=> item.price.current + (item.price.current * 0.1);


    return(
        <Paper
            elevation={0}
            sx={{
                display: 'flex',
                alignItems: 'center',
                height: 80,
                mt: 3,
                px: {
                    xs: 2, // padding   xs ( 600px)
                    sm: 2, // padding   sm (600px  )
                    md: 3, // padding   md (960px  )
                    lg: 4, // padding   lg (1280px  )
                    xl: 5, // padding   xl (1920px  )
                },
                borderRadius: '15px',
            }}
        >
            
            <Box 
                sx={{ 
                    minWidth: {
                        xs: '150px',
                        md: '180px',
                    },
                    display: 'flex' 
                }}
            >
                <Typography color="black"
                    sx={{
                        fontFamily: 'Kinetika, sans-serif',
                        fontWeight: 800,
                        fontSize: {
                            xs: 18,
                            md: 20,
                        },
                        lineHeight: '1', // 100%
                        letterSpacing: '0px', // 0%
                        textTransform: 'uppercase',
                        textAlign: 'center'
                    }}
                >
                     { item.price?.current } 
                </Typography>
                <Typography color="black"
                    sx={{
                        textDecoration: 'line-through',
                        fontFamily: 'Kinetika, sans-serif',
                        mt: 0.5,
                        ml: 1,
                        fontWeight: 800,
                        fontSize: {
                            xs: 16,
                            md: 18,
                        },
                        lineHeight: '1',
                        textAlign: 'center',
                        color: '#9F9F9F'
                    }}
                >
                    { item?.price?.old || calculate() } 
                </Typography>
            </Box>

            <Box 
                sx={{
                    marginLeft: {
                        xs: 3, // padding   xs ( 600px)
                        sm: 5, // padding   sm (600px  )
                        md: 15, // padding   md (960px  )
                        lg: 10, // padding   lg (1280px  )
                        xl: 15, // padding   xl (1920px  )
                    }
                }}
            >
                <Typography color="black"
                    sx={{
                        fontFamily: 'Kinetika, sans-serif',
                        ml: 1,
                        fontWeight: 400,
                        fontSize: {
                            xs: '12px'
                        },
                        lineHeight: '1',
                        textAlign: 'left',
                        textTransform: 'uppercase',
                    }}
                >
                           
                    <a href="#table">   </a>
                </Typography>
            </Box>
        </Paper>
    );
}