import React from "react";
import { ItemProduct } from '../index';
import { Box, Paper, Typography } from "@mui/material";
import NavigationTab from '../../component/navigation';
import Table from '../../component/table';


const Description =()=> {
    return(
        <React.Fragment>
            <Typography color="black"
                sx={{
                    fontFamily: 'Kinetika, sans-serif',
                    fontWeight: 800,
                    fontSize: '20px',
                    lineHeight: '1',
                    textAlign: 'left',
                    my: 'auto',
                }}
            >
                 
            </Typography>
            <Typography color="black"
                sx={{
                    fontFamily: 'Kinetika, sans-serif',
                    fontWeight: 100,
                    fontSize: '16px',
                    lineHeight: '1',
                    textAlign: 'left',
                    my: 'auto',
                    mt: 2
                }}
            >
                  FTP-V (CH)           PROFI. - ,       topSafe,    .
            </Typography>
            <Typography color="black"
                sx={{
                    fontFamily: 'Kinetika, sans-serif',
                    fontWeight: 800,
                    fontSize: '20px',
                    lineHeight: '1',
                    textAlign: 'left',
                    my: 'auto',
                    mt: 4
                }}
            >
                 
            </Typography>
            <Typography color="black"
                sx={{
                    fontFamily: 'Kinetika, sans-serif',
                    fontWeight: 100,
                    fontSize: '16px',
                    lineHeight: '1',
                    textAlign: 'left',
                    my: 'auto',
                    mt: 2
                }}
            >
                  FTP-V (CH)           PROFI. - ,       topSafe,    .
            </Typography>
    </React.Fragment>
    );
}


/**
 * 
 * 
 */
export default function ({ item, onTableChange }: { item: ItemProduct }) {
    const [activeIndex, setActiveIndex] = React.useState(0);
    const handlerChangeNavigationTab =(index: number)=> {
        setActiveIndex(index);
        console.log(index);
    }


    return (
        <section className="CenterCatalogItem">
            {/*     */}
            <Table 
                onChange={onTableChange}
            />

            {/*      */}
            <NavigationTab 
                sx={{
                    mt: 5
                }}
                activeIndex={activeIndex} 
                onChange={handlerChangeNavigationTab}
            />

            {/*    */}
            <Paper
                elevation={0}
                sx={{
                    display: 'flex',
                    alignItems: 'center',
                    borderRadius: '30px',
                    mt: 3
                }}
            >
                <Box sx={{ display: 'flex', flexDirection: 'column' }}>
                    <Box 
                        sx={{ 
                            p: {
                                xs: 3, // padding   xs ( 600px)
                                sm: 3, // padding   sm (600px  )
                                md: 3, // padding   md (960px  )
                                lg: 4, // padding   lg (1280px  )
                                xl: 5, // padding   xl (1920px  )
                            },
                            mt: 2, 
                            display: 'flex', 
                            flexDirection: 'column' 
                        }}
                    >
                        { activeIndex === 0 && <Description /> }
                    </Box>
                </Box>
            </Paper>
            
        </section>
    );
}